import { useState } from 'react';
import {GrOrganization,GrLink,GrLocation,GrBook, GrTwitter, GrClock} from 'react-icons/gr'
function App() {

  const [data,setData] = useState('')

  const getData = (e) => {
    fetch(`https://api.github.com/users/${e.target.value}`)
    .then(response => response.json())
    .then(response => setData(response))
  }
  

  return (
    <div className="App">
      <div className="main">
      <div className="header">
        <h2 className="title">Search GitHub Users</h2>
        <input type="text" onChange={getData} className="search"/>
      </div>
      <div className="user">
        {data.avatar_url && (
          <div className="header">
              <img src={data.avatar_url} alt="Profile" />
              <a href={data.url} className="user-name">{data.login}</a>
          </div>
        )}
        <div className="info">
            {data.company && (
              <div className="user-info">
                <GrOrganization/>
                {data.company}
              </div>
            )}
            {data.location && (
              <div className="user-info">
                <GrLocation/>
                {data.location}
              </div>
            )}
            {data.blog && (
              <div className="user-info">
                <GrLink/>
                {data.blog}
              </div>
            )}
            {data.twitter_username && (
              <div className="user-info">
                <GrTwitter/>
                {data.twitter_username}
              </div>
            )}
            {data.public_repos && (
              <div className="user-info">
                <GrBook/>
                {data.public_repos}
              </div>
            )}
            {data.created_at && (
              <div className="user-info">
                <GrClock/>
                {data.created_at}
              </div>
            )}
        </div>
      </div>
      </div>
    </div>
  );
}

export default App;
